.payment {
    background-color: white;
}

.paymentContainer > h1 {
    text-align: center;
    padding: 10px;
    font-weight: 400;
    background-color: rgb(234,237,237);
    border-bottom: 1px solid lightgray;
}

.paymentContainer > h1 a {
    text-decoration: none;
    color: royalblue;
}


.paymentSection {
    display: flex;
    padding: 20px;
    margin: 0 20px;
    border-bottom: 1px solid lightgray;
}

.paymentTitle {
    flex: 0.2;
}

.paymentAddress, .paymentItems, .paymentDetails {
    flex: 0.8;
}

.paymentDetails > form {
    max-width: 400px;
}

.paymentDetails > h3 {
    padding-bottom: 20px;
}

.paymentDetails > form > div > button {
    background: #f0c14b;
    border-radius: 2px;
    width: 100%;
    height: 30px;
    border: none;
    font-weight: bolder;
    margin-top: 10px;
    color: #111;
    cursor: pointer;
}

.paymentDetails > form > div > button:hover {
    border: 1px solid #a88734 #9c7e31 #846a29;
}