.product {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    margin: 10px;
    padding: 20px;
    width: 100%;
    max-height: 400px;
    min-width: 100px;
    background-color: white;
    z-index: 1;
    border-radius: 5px;
}

.product > img {
    max-height: 200px;
    width: 100%;
    object-fit: contain;
    margin-bottom: 15px;
}

.product > button {
    background: #f0c14b;
    margin-top: 10px;
    color: #111;
    border: none;
    border-radius: 5px;
    padding: 5px;
}

.productPrice {
    margin-top: 5px;
}

.productInfo {
    width: 100%;
    height: 100px;
    margin-bottom: 15px;
}

.productRating {
    display: flex;
}

