.order {
    padding: 40px;
    margin: 20px 0;
    border: 1px solid lightgray;
    background-color: white;
    position: relative;
}

.orderId {
    /* Parent is relative thats why it stays inside container */
    position: absolute;
    top: 40px;
    right: 20px;
}

.orderTotal {
    font-weight: 500;
    text-align: right;
}